.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0px;
  background-color: rgb(212, 132, 4);
  min-width: 100%;
}

.portfolio-header {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: rgb(20, 33, 61);
  text-align: center;
}

.portfolio-wrapper {
  width: 90%;
  display: flex;
  flex-wrap: wrap;

}

.portfolio-item {
  width: 100%;
  text-align: center;
  margin-bottom: 36px;
  background-color: rgb(229, 229, 229);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
  padding: 16px;
}

.portfolio-item p {
  font-size: 20px;
  font-weight: bold;
  margin-top: 0;
}

.portfolio-item img {
  height: 100px;
  width: auto;
  border: 1px solid rgb(20, 33, 61);
}

.item-links {
  display: flex;
  flex-direction: column;
  padding: 20px 0px 10px 0px;
}

.item-links a {
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .portfolio-wrapper {
    gap: 5%;
    justify-content: center;
  }

  .portfolio-item {
    width: 40%;
  }

  .portfolio-item img {
    height: 120px;
  }
}

@media (min-width: 992px) {
  .portfolio {
    padding: 50px 0px;
  }

  .portfolio-header {
    margin-bottom: 30px;
  }

  .portfolio-wrapper {
    width: 80%;
  }

  .portfolio-item img {
    max-height: 200px;
  }
}
